import { useState, useEffect } from "react";
import Formulario from "./components/Formulario";
import ListadoImagenes from "./components/ListadoImagenes";


function App() {

  // state de la app
  const [ busqueda, setBusqueda ] = useState( '' );
  const [ imagenes, setImagenes ] = useState( [] );
  const [ paginaActual, setPaginaActual ] = useState( 1 );
  const [ totalPaginas, setTotalPaginas ] = useState( 1 );

  useEffect( () => {

    const consultarApi = async () => {
      if ( busqueda === '' ) {
        return;
      }

      const imagenesPorPagina = 30;
      const key = '22390393-d040c1ffcd267f9fbf6d5b22b';
      const url = `https://pixabay.com/api/?key=${ key }&q=${ busqueda }&per_page=${ imagenesPorPagina }&page=${ paginaActual }`;

      const respuesta = await fetch( url );
      const resultado = await respuesta.json();

      setImagenes( resultado.hits );

      // Calcular el total de páginas
      const calcularTotalPaginas = Math.ceil( resultado.totalHits / imagenesPorPagina );
      setTotalPaginas( calcularTotalPaginas );

      // Mover la pantalla hacia arriba
      const jumbotron = document.querySelector( '.jumbotron' );
      jumbotron.scrollIntoView( { behavior: 'smooth' } );

    }

    consultarApi();

  }, [ busqueda, paginaActual ] )


  // Definir la página anterior
  const paginaAnterior = () => {
    const nuevaPaginaActual = paginaActual - 1;
    if ( nuevaPaginaActual === 0 ) {
      return;
    }
    setPaginaActual( nuevaPaginaActual );
  }

  // Definir la página siguiente
  const paginaSiguiente = () => {
    const nuevaPaginaActual = paginaActual + 1;
    if ( nuevaPaginaActual > totalPaginas ) {
      return;
    }
    setPaginaActual( nuevaPaginaActual );
  }


  return (
    <div className="container">
      <div className="jumbotron">
        <p className="lead text-center">
          Pixabay API Image Search
        </p>

        <Formulario
          setBusqueda={ setBusqueda }

        />

      </div>

      <div className="row justify-content-center">
        <ListadoImagenes
          imagenes={ imagenes }
        />

        { ( paginaActual === 1 ) ?
          null
          :
          ( <button
            type="button"
            className="btn btn-info mr-1"
            onClick={ paginaAnterior }
          >&lsaquo; Previous</button> ) }

        { ( paginaActual === totalPaginas ) ?
          null
          :
          ( <button
            type="button"
            className="btn btn-info"
            onClick={ paginaSiguiente }
          >Next &rsaquo;</button> )
        }

      </div>

      <footer style={ { textAlign: 'end' } }>
        <a href="https://pixabay.com/">
          <img src="https://pixabay.com/static/img/logo.svg" alt="Pixabay" style={ { maxHeight: '35px', marginTop: '20px' } } />
        </a>

        <a href="https://pixabay.com/" style={ { margin: '3px 15px 5px 0', fontSize: '12px', lineHeight: '1.7', color: '#555', display: 'block', float: 'left', padding: '9px 12px 6px', border: '1px solid #ccc' } }>
          <i style={ { display: 'block', width: '68px', height: '18px', overflow: 'hidden' } }>
            <img src="https://pixabay.com/static/img/logo.svg" alt="Pixabay" style={ { width: '94px' } } />
          </i> Free Images
        </a>
      </footer>
    </div>
  );
}

export default App;
