import { useState } from "react";
import Error from "./Error";
import PropTypes from 'prop-types';

const Formulario = ( { setBusqueda } ) => {

  // UseState para guardar lo que el usuario busque
  const [ termino, setTermino ] = useState( '' );
  const [ error, setError ] = useState( false );


  // Al hacer click en submit:
  const buscarImagenes = e => {
    e.preventDefault();

    // Validar
    if ( termino.trim() === '' ) {
      setError( true );
      return;
    }
    setError( false );


    // Enviar el término de búsqueda al componente principal
    setBusqueda( termino );


  }

  return (
    <form
      onSubmit={ buscarImagenes }
    >
      <div className="row">

        <div className="form-group col-md-8">
          <input
            type="text"
            className="form-control form-control-lg"
            placeholder="Search for Images, e.g.: Forest"
            onChange={ e => setTermino( e.target.value ) }
            name=""
            id=""
          />
        </div>

        <div className="form-group col-md-4">
          <input
            type="submit"
            className="btn btn-lg btn-danger btn-block"
            value="Buscar"
          />
        </div>


      </div>

      { error ?
        <Error mensaje="Agrega un término de búsqueda" />
        :
        null
      }


    </form>
  );
}

Formulario.propTypes = {
  setBusqueda: PropTypes.func.isRequired
};

export default Formulario;